<template>
  <div>
    <nav aria-label="Page navigation example">
      <ul class="pagination">
        <li class="page-item" :class="{ disabled: !pageData.has_pre }">
          <a
            class="page-link"
            href="#"
            aria-label="Previous"
            @click.prevent="updatePage(pageData.current_page - 1)"
          >
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li
          class="page-item"
          v-for="page in pageData.total_pages"
          :key="page"
          :class="{ active: pageData.current_page === page }"
        >
          <a class="page-link" href="#" @click.prevent="updatePage(page)">{{
            page
          }}</a>
        </li>
        <li class="page-item" :class="{ disabled: !pageData.has_next }">
          <a
            class="page-link"
            href="#"
            aria-label="Next"
            @click.prevent="updatePage(pageData.current_page + 1)"
          >
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
export default {
  name:'pagination',
  props:{                           //從Product傳進的物件
    pageData:{
      type : Object,
      default : {},
    },
  },
  methods:{
    updatePage(page){                       //傳出參數給product的方法
      this.$emit('emitPage',page)
    }
  },
}
</script>


