//官方元件
import Vue from 'vue';
import VueRouter from 'vue-router';

//自訂分頁元件
import Home from '@/components/frontend/Home';
import Login from '@/components/backend/Login';
import CustomerOrder from '@/components/frontend/CustomerOrders';
import CustomerCheckout from '@/components/frontend/CustomerCheckout';
import HomeContent from '@/components/frontend/HomeContent';
import About from '@/components/frontend/About';
import Contact from '@/components/frontend/Contact';
import Dashboard from '@/components/backend/Dashboard';
import Products from '@/components/backend/Products';

Vue.use(VueRouter);
 
export default new VueRouter({
    routes:[
        {
            path: '*', //如果路徑為其他未定義的，則會導向到首頁,避免用戶進入不存在頁面
            redirect: '/',
        },
        {
            path: '/',//對應的虛擬路徑
            // name: 'Home',//元件呈現名稱
            component: Home,//對應元件,即import的名稱
            children:[
                {
                    path: '',
                    name: 'Home',
                    component: HomeContent,
                },
                {
                    path: 'customer_order',
                    name: 'CustomerOrder',
                    component: CustomerOrder,
                },
                {
                    path: 'about',
                    name: 'About',
                    component: About,
                },
                {
                    path: 'contact',
                    name: 'Contact',
                    component: Contact,
                },
                {
                    path: 'customer_checkout/:orderId',
                    name: 'CustomerCheckout',
                    component: CustomerCheckout,
                },   
            ],
        },
        {
            path: '/login',
            name: 'Login',
            component: Login,
        },
        {
            path: '/admin',
            name: 'Dashboard',
            component: Dashboard,
            children:[
                {
                    path: 'products',
                    name: 'Products',
                    component: Products,
                    meta: { requiresAuth: true },//需驗證    
                },
                        
            ],
        },
    ],
    
});


