<template>
  <div>
    <Header></Header>
    <router-view></router-view>
    <Footer></Footer>
    <!-- 回到最上方按鈕 -->
    <div>
      <button
        v-if="scrollvalue"
        class="btn btn-danger backtotop"
        @click="scrolltotop"
      >
        <i class="fas fa-arrow-up"></i>
      </button>
    </div>
     <!-- 購物車Modal -->
    <div
      class="modal fade"
      id="cartModal"
      tabindex="-1"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title font-weight-bold ml-auto" id="exampleModalLabel">購物車清單</h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="my-5 row justify-content-center" v-if="cart.final_total > 0">
      <div class="col-md-6">
        <table class="table" >
          <thead>
            <th></th>
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
          </thead>
          <tbody>
            <tr v-for="item in cart.carts" :key="item.id">
              <td class="align-middle">
                <button
                  type="button"
                  class="btn btn-outline-danger btn-sm"
                  @click="removeCartItem(item.id)"
                >
                  <i class="far fa-trash-alt"></i>
                </button>
              </td>
              <td class="align-middle">
                {{ item.product.title }}
                <!-- <div class="text-success" v-if="item.coupon">
          已套用優惠券
        </div> -->
              </td>
              <td class="align-middle">
                {{ item.qty }}/{{ item.product.unit }}
              </td>
              <td class="align-middle text-right">{{ item.final_total }}元</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="3" class="text-right">總計</td>
              <td class="text-right">{{ cart.total }}元</td>
            </tr>
            <!-- 隱藏此格，有折扣才會出現 -->
            <tr v-if="cart.final_total !== cart.total">
              <td colspan="3" class="text-right text-success">折扣價</td>
              <td class="text-right text-success">{{ cart.final_total }}元</td>
            </tr>
          </tfoot>
        </table>
      </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
            >
              關閉
            </button>
            <router-link class="btn btn-danger" to="/customer_checkout">前往結帳</router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from "./Header";
import Footer from "./Footer";
import HomeContent from "./HomeContent";
import CustomerOrder from "./CustomerOrders";
import About from "./About";
import Contact from "./Contact";

export default {
  components: {
    Header,
    Footer,
    HomeContent,
    CustomerOrder,
    About,
    Contact,
  },
  data() {
    return {
      scrollvalue: false,
      products: [],
      product: {},
      isLoading: false,
      status: {
        loadingItem: "",
      },
      form: {
        user: {
          name: "",
          email: "",
          tel: "",
          address: "",
        },
        message: "",
      },
      cart: {
        carts:[],
      },
      pagination: {},
    };
  },
  methods: {
    //滾動事件語法，最到最上方
    scrolltotop() {
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
    btnscroll() {
      var scrollTop =
        document.body.scrollTop || document.documentElement.scrollTop;
      if (scrollTop > 500) {
        this.scrollvalue = true;
      } else {
        this.scrollvalue = false;
      }
    },
    getProducts(page = 1) {
      //取得遠端產品資料
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`;
      const vm = this;
      //API伺服器路徑及自訂API路徑,這兩項因不同API會改變,因此特別抓出來做變數
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        //看遠端撈的資料變數與所需的是否一致
        vm.isLoading = false;
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
      });
    },
    getProduct(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      vm.status.loadingItem = id;
      this.$http.get(url).then((response) => {
        vm.product = response.data.product;
        $("#productModal").modal("show");
        vm.product.num = ""; //使產品數量為空,避免select選單遭綁定
        console.log(response);
        vm.status.loadingItem = "";
      });
    },
    addtoCart(id, qty = 1) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.status.loadingItem = id;
      const cart = {
        product_id: id,
        qty,
      };
      this.$http.post(url, { data: cart }).then((response) => {
        vm.status.loadingItem = "";
        vm.getCart();
        $("#productModal").modal("hide");
      });
    },
    getCart() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        vm.cart = response.data.data;
        vm.isLoading = false;
      });
    },
    removeCartItem(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      vm.isLoading = true;
      this.$http.delete(url).then(() => {
        vm.getCart();
        vm.isLoading = false;
      });
    },
    createOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      const order = vm.form;
      // vm.isLoading = true;
      this.$http.post(url, { data: order }).then((response) => {
        if (response.data.success) {
          vm.$router.push(`/customer_checkout/${response.data.orderId}`);
        } else {
          console.log("購物車沒東西");
        }
        vm.isLoading = false;
      });
    },
  },
  created() {
    this.getProducts();
    this.getCart();
  },
  //監聽滾動事件
  mounted() {
    window.addEventListener("scroll", this.btnscroll);
  },
};
</script>

<style scoped>
.btn {
  border-width: 2px;
}
.bg-banner1 {
  background-image: url(../../assets/img/tea1.jpg);
}
.bg-banner2 {
  background-image: url(../../assets/img/tea2.jpg);
}
.bg-banner3 {
  background-image: url(../../assets/img/tea3.jpg);
}
.header-carousel-item {
  height: 40rem;
}

.bg-cover {
  background-size: cover;
  background-position: center center;
}
.bg-imgleft {
  position: absolute;
  top: 0;
  bottom: 0;
  background-image: url(../../assets/img/tea4.jpg);
}
.bg-imgright {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  background-image: url(../../assets/img/tea5.jpg);
}
.bg-text1 {
  color: #a9a9a9;
}
.carousel-caption {
  background-color: rgba(0, 0, 0, 0.4);
}

.font-title {
  font-size: 28px;
}

.border-none {
  border: none !important;
}
.backtotop {
  position: fixed;
  bottom: 0;
  right: 0;
  border-radius: 50%;
  margin-right: 5px;
  margin-bottom: 5px;
}
@media (min-width: 768px) {
  .text-md-dark {
    color: #333;
  }
}
</style>


