<template>
  <div class="container">
    <loading :active.sync="isLoading"></loading>
    <div class="row mt-4">
      <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
        <div class="card border-0 shadow-sm">
          <div
            style="
              height: 150px;
              background-size: cover;
              background-position: center;
            "
            :style="{ backgroundImage: `url(${item.imageUrl})` }"
          ></div>
          <div class="card-body">
            <span class="badge badge-secondary float-right ml-2">{{
              item.category
            }}</span>
            <h5 class="card-title">
              <a href="#" class="text-dark">{{ item.title }}</a>
            </h5>
            <p class="card-text">{{ item.content }}</p>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h5" v-if="!item.price">
                {{ item.origin_price | currency }} 元
              </div>
              <del class="h6" v-if="item.price"
                >原價 {{ item.origin_price | currency }} 元</del
              >
              <div class="h5" v-if="item.price">
                現在只要 {{ item.price | currency }} 元
              </div>
            </div>
          </div>
          <div class="card-footer d-flex">
            <button
              type="button"
              class="btn btn-outline-success btn-sm"
              @click="getProduct(item.id)"
            >
              <i
                class="fas fa-spinner fa-spin"
                v-if="status.loadingItem === item.id"
              ></i>
              查看更多
            </button>
            <button
              type="button"
              class="btn btn-outline-danger btn-sm ml-auto"
              @click="addtoCart(item.id)"
            >
              <i
                class="fas fa-spinner fa-spin"
                v-if="product.id === status.loadingItem"
              ></i>
              加到購物車
            </button>
          </div>
        </div>
    </div>
    </div>
    <Pagination :pageData="pagination" @emitPage="getProducts"></Pagination>
    <!-- 查看更多的單一產品modal -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">
              {{ product.title }}
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="text-center">
              <img :src="product.imageUrl" class="img-fluid" alt="">
            </div>
            <blockquote class="blockquote mt-3">
              <p class="mb-0">{{ product.content }}</p>
              <footer class="blockquote-footer text-right">
                {{ product.description }}
              </footer>
            </blockquote>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h4" v-if="!product.price">
                {{ product.origin_price | currency }} 元
              </div>
              <del class="h6" v-if="product.price"
                >原價 {{ product.origin_price | currency }} 元</del
              >
              <div class="h4" v-if="product.price">
                現在只要 {{ product.price | currency }} 元
              </div>
            </div>
            <select name="selectItems" class="form-control mt-3" v-model="product.num">
              <option value="" disabled selected>--請選擇--</option>
              <option :value="num" v-for="num in 10" :key="num">
                選購 {{ num }} {{ product.unit }}
              </option>
            </select>
          </div>
          <div class="modal-footer">
            <div class="text-muted text-nowrap mr-3">
              小計 <strong>{{ product.num * product.price | currency }}</strong> 元
            </div>
            <button
              type="button"
              class="btn btn-primary"
              @click="addtoCart(product.id, product.num)"
            >
              <i
                class="fas fa-spinner fa-spin"
                v-if="product.id === status.loadingItem"
              ></i>
              加到購物車
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- 購物車內容 -->
    <div class="my-5 row justify-content-center" v-if="cart.final_total > 0">
      <div class="col-md-6">
        <table class="table" >
          <thead>
            <th></th>
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
          </thead>
          <tbody>
            <tr v-for="item in cart.carts" :key="item.id">
              <td class="align-middle">
                <button
                  type="button"
                  class="btn btn-outline-danger btn-sm"
                  @click="removeCartItem(item.id)"
                >
                  <i class="far fa-trash-alt"></i>
                </button>
              </td>
              <td class="align-middle">
                {{ item.product.title }}
                <!-- <div class="text-success" v-if="item.coupon">
          已套用優惠券
        </div> -->
              </td>
              <td class="align-middle">
                {{ item.qty }}/{{ item.product.unit }}
              </td>
              <td class="align-middle text-right">{{ item.final_total }}元</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="3" class="text-right">總計</td>
              <td class="text-right">{{ cart.total }}元</td>
            </tr>
            <!-- 隱藏此格，有折扣才會出現 -->
            <tr v-if="cart.final_total !== cart.total">
              <td colspan="3" class="text-right text-success">折扣價</td>
              <td class="text-right text-success">{{ cart.final_total }}元</td>
            </tr>
          </tfoot>
        </table>
      </div>
    </div>
    <!-- 表單內容 -->
    <div class="my-5 row justify-content-center" v-if="cart.final_total > 0">
      <div class="col-md-6">
        <validation-observer v-slot="{ invalid }">
          <form @submit.prevent="createOrder">
            <validation-provider
              rules="required|email"
              class="form-group"
              tag="div"
              v-slot="{ errors, classes, passed }"
            >
              <!-- 輸入框 -->
              <label for="useremail">Email</label>
              <input
                id="useremail"
                type="email"
                name="email"
                v-model="form.user.email"
                class="form-control"
                :class="classes"
                placeholder="請輸入 Email"
              />
              <!-- 錯誤訊息 -->
              <span class="invalid-feedback">{{ errors[0] }}</span>
              <span v-if="passed" class="valid-feedback">Email 正確</span>
            </validation-provider>
            <validation-provider
              rules="required"
              class="form-group"
              tag="div"
              v-slot="{ errors, classes, passed }"
            >
              <!-- 輸入框 -->
              <label for="username">收件人姓名</label>
              <input
                id="username"
                type="text"
                name="姓名"
                v-model="form.user.name"
                class="form-control"
                :class="classes"
                placeholder="請輸入姓名"
              />
              <!-- 錯誤訊息 -->
              <span class="invalid-feedback">{{ errors[0] }}</span>
              <span v-if="passed" class="valid-feedback"></span>
            </validation-provider>
            <validation-provider
              rules="required"
              class="form-group"
              tag="div"
              v-slot="{ errors, classes, passed }"
            >
              <label for="usertel">收件人電話</label>
              <input
                id="usertel"
                type="tel"
                name="電話"
                v-model="form.user.tel"
                class="form-control"
                :class="classes"
                placeholder="請輸入電話"
              />
              <span class="invalid-feedback">{{ errors[0] }}</span>
              <span v-if="passed" class="valid-feedback"></span>
            </validation-provider>
            <validation-provider
              rules="required"
              class="form-group"
              tag="div"
              v-slot="{ errors, classes, passed }"
            >
              <label for="useraddress">收件人地址</label>
              <input
                id="useraddress"
                type="address"
                name="地址"
                v-model="form.user.address"
                class="form-control"
                :class="classes"
                placeholder="請輸入地址"
              />
              <span class="invalid-feedback">{{ errors[0] }}</span>
              <span v-if="passed" class="valid-feedback"></span>
            </validation-provider>
            <div class="form-group">
              <label for="useraddress">留言</label>
              <textarea
                name=""
                id=""
                class="form-control"
                cols="30"
                rows="10"
                v-model="form.message"
              ></textarea>
            </div>
            <div class="text-right">
              <button type="submit" class="btn btn-primary" :disabled="invalid">
                送出訂單
              </button>
            </div>
          </form>
        </validation-observer>
      </div>
    </div>
    <!-- 購物車Modal -->
    <div
      class="modal fade"
      id="cartModal"
      tabindex="-1"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title font-weight-bold ml-auto" id="exampleModalLabel">購物車清單</h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="my-5 row justify-content-center" v-if="cart.final_total > 0">
      <div class="col-md-6">
        <table class="table" >
          <thead>
            <th></th>
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
          </thead>
          <tbody>
            <tr v-for="item in cart.carts" :key="item.id">
              <td class="align-middle">
                <button
                  type="button"
                  class="btn btn-outline-danger btn-sm"
                  @click="removeCartItem(item.id)"
                >
                  <i class="far fa-trash-alt"></i>
                </button>
              </td>
              <td class="align-middle">
                {{ item.product.title }}
                <!-- <div class="text-success" v-if="item.coupon">
          已套用優惠券
        </div> -->
              </td>
              <td class="align-middle">
                {{ item.qty }}/{{ item.product.unit }}
              </td>
              <td class="align-middle text-right">{{ item.final_total }}元</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="3" class="text-right">總計</td>
              <td class="text-right">{{ cart.total }}元</td>
            </tr>
            <!-- 隱藏此格，有折扣才會出現 -->
            <tr v-if="cart.final_total !== cart.total">
              <td colspan="3" class="text-right text-success">折扣價</td>
              <td class="text-right text-success">{{ cart.final_total }}元</td>
            </tr>
          </tfoot>
        </table>
      </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
            >
              關閉
            </button>
            <router-link class="btn btn-danger" to="/customer_checkout">前往結帳</router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Pagination from "../Pagination";

export default {
  components:{
    Pagination,
  },
  data() {
    return {
      products: [],
      product: {},
      isLoading: false,
      status: {
        loadingItem: "",
      },
      form: {
        user: {
          name: "",
          email: "",
          tel: "",
          address: "",
        },
        message: "",
      },
      cart: {},
      pagination: {},
    };
  },
  methods: {
    getProducts(page=1) {
      //取得遠端產品資料
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`;
      const vm = this;
      //API伺服器路徑及自訂API路徑,這兩項因不同API會改變,因此特別抓出來做變數
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        //看遠端撈的資料變數與所需的是否一致
        vm.isLoading = false;
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
      });
    },
    getProduct(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      vm.status.loadingItem = id;
      this.$http.get(url).then((response) => {
        vm.product = response.data.product;
        $("#productModal").modal("show");
        vm.product.num = ''; //使產品數量為空,避免select選單遭綁定
        console.log(response);
        vm.status.loadingItem = "";
      });
    },
    addtoCart(id, qty = 1) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.status.loadingItem = id;
      const cart = {
        product_id: id,
        qty,
      };
      this.$http.post(url, { data: cart }).then((response) => {
        vm.status.loadingItem = "";
        vm.getCart();
        $("#productModal").modal("hide");
      });
    },
    getCart() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        vm.cart = response.data.data;
        vm.isLoading = false;
      });
    },
    removeCartItem(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      vm.isLoading = true;
      this.$http.delete(url).then(() => {
        vm.getCart();
        vm.isLoading = false;
      });
    },
    createOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      const order = vm.form;
      // vm.isLoading = true;
      this.$http.post(url, { data: order }).then((response) => {
        if(response.data.success){
            vm.$router.push(`/customer_checkout/${response.data.orderId}`);
          }else{
            console.log('購物車沒東西');
          }
        vm.isLoading = false;
      });
    },
  },
  created() {
    this.getProducts();
    this.getCart();
  },
};
</script>


