// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
//第三方套件
import Vue from 'vue';
import axios from 'axios'; //主要AJAX套件
import VueAxios from 'vue-axios'; //將它轉為vue套件,即可用use方法運行
import Loading from 'vue-loading-overlay';//載入讀取畫面套件及其CSS
import 'vue-loading-overlay/dist/vue-loading.css';
import 'bootstrap';//為了product可以啟用modal之JQ語法
//vee-validate表單驗證
import { ValidationObserver, ValidationProvider, extend, localize, configure } from 'vee-validate';
import TW from 'vee-validate/dist/locale/zh_TW.json';
import * as rules from 'vee-validate/dist/rules';

//自定義套件
import App from './App';
import router from './router';
import currencyFilter from './filters/currency';

Vue.use(VueAxios, axios);
Vue.config.productionTip = false;
//vee-validata啟用
Object.keys(rules).forEach((rule) => {
  extend(rule, rules[rule]);
});
 
localize('zh_TW', TW);
 
Vue.component('ValidationObserver', ValidationObserver); //多項的
Vue.component('ValidationProvider', ValidationProvider); //單一的
 
configure({
  classes: {
    valid: 'is-valid',
    invalid: 'is-invalid',
  }
});

Vue.component('Loading',Loading);//全域啟用vue-loading-overlay
Vue.filter('currency',currencyFilter);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  router,
});

//判斷是否需驗證頁面,this.$http語法是.vue元件內才能使用,這裡router下使用axios開頭
router.beforeEach((to, from, next) => {
  if(to.meta.requiresAuth){
    const api = `${process.env.APIPATH}/api/user/check`;
    axios.post(api).then((response) => {
    if (response.data.success) {
      next(); //驗證成功則放行
    }else{
      console.log('請重新登入');
      next({
        path:'/login',
      });//失敗則導回login頁面
    }
  });
  }else{
    next();//不需驗證則放行
  }
});



